export const user = {
  state:{
    name:'晓冬',
    age:20,
  },
  mutations:{
    change(state,val){
      console.log('你将我的名字改了');
      state.name = val;
    },
    judge(state,val){
      if(val>state.age){
        alert('你的年龄比我大');
      }else{
        alert('你的年龄比我小');
      }
    }
  },
  actions:{
    getMutations(content,data){
      console.log(data)
      console.log(content);
      console.log(content.state);
      console.log(content.mutations);
      content.commit('change',data)
    }
  }
}



