import Vue from 'vue'
import Router from 'vue-router'

// 引入 会员中心组件
Vue.use(Router)

const index = resolve => {
  require.ensure(['./../components/index/index.vue'], (require) => {
      resolve(require('./../components/index/index.vue'))
  },'index')
}
const photos = resolve => {
  require.ensure(['./../components/photos/photos.vue'],(require) => {
    resolve(require('./../components/photos/photos.vue'))
  },'photos')
}
const logs = resolve => {
  require.ensure(['./../components/logs/logs.vue'],(require) => {
    resolve(require('./../components/logs/logs.vue'))
  },'logs')
}
const message = resolve => {
  require.ensure(['./../components/message/message.vue'],(require) => {
    resolve(require('./../components/message/message.vue'))
  },'message')
}
const login = resolve => {
  require.ensure(['./../components/login/login.vue'],(require) => {
    resolve(require('./../components/login/login.vue'))
  },'login')
}
const write = resolve => {
  require.ensure(['./../components/write/write.vue'],(require) => {
    resolve(require('./../components/write/write.vue'))
  },'write')
}
const demo = resolve => {
  require.ensure(['./../components/demo.vue'],(require) => {
    resolve(require('./../components/demo.vue'))
  },'demo')
}


export default new Router({
 mode:'history',
 routes:[
   {
     path:"/",
     component:index,
   },{
     path:"/photos",
     component:photos,
   },{
     path:"/mylog",
     component:logs
   },{
     path:"/message",
     component:message,
   },
   {
     path:"/login",
     component:login
   },
   {
     path:"/write",
     component:write,
   },{
     path:'/demo',
     component:demo,
   }
 ]
})



